/* poppins-regular - latin */
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Poppins"),
    url("../../fonts/Poppinsregular.woff2") format("woff2"),
    url("../../fonts/Poppinsregular.woff") format("woff");
}
